import React from 'react';
import {NavLink} from 'react-router-dom';
import classes from './MainMenu.module.css';

const mainMenu = () => {
    return (
        <div className={classes.MainMenuWrapper}>
            <h4>ΕΠΙΛΕΞΤΕ ΚΑΤΗΓΟΡΙΑ</h4>
            <div className={classes.MainMenu}>
                <NavLink to='/beverages'>
                    <div className={classes.Item}><h3>ΡΟΦΗΜΑΤΑ</h3></div>
                </NavLink>
                <NavLink to='/drinks'>
                    <div className={classes.Item}><h3>ΠΟΤΑ</h3></div>
                </NavLink>
                <NavLink to='/beers'>
                    <div className={classes.Item}><h3>ΜΠΥΡΕΣ</h3></div>
                </NavLink>
                <NavLink to='/wines'>
                    <div className={classes.Item}><h3>ΚΡΑΣΙΑ</h3></div>
                </NavLink>
                <NavLink to='/cocktails'>
                    <div className={classes.Item}><h3>COCKTAILS</h3></div>
                </NavLink>
                <NavLink to='/food'>
                    <div className={classes.Item}><h3>ΦΑΓΗΤΑ</h3></div>
                </NavLink>
            </div>
        </div>
    )
}

export default mainMenu;
